<template lang="html">
  <section class="menu-item-create">
    <el-form
      :model="formData"
      :rules="rules"
      label-width="120px"
      ref="itemCreateForm"
    >
      <el-form-item label="" prop="parentId">
        <el-select v-model="formData.parentId" placeholder="" style="width: 100%;">
          <el-option key="root" label="root" :value="0"/>
          <template v-for="item in selectOptions">
            <el-option v-if="!item.parentId" :key="item.name" :label="item.name" :value="item.id" />
          </template>
        </el-select>
      </el-form-item>

      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" placeholder="" />
      </el-form-item>

      <el-form-item label="" prop="url">
        <el-input v-model="formData.url" placeholder="" />
      </el-form-item>

      <el-form-item label="" prop="css">
        <el-input v-model="formData.css" placeholder="FontAwesome" style="width: 85%;" />
        <span class="pull-right"><i class="fa" :class="formData.css"></i></span>
      </el-form-item>

      <el-form-item label="" prop="sort">
        <el-input v-model="formData.sort" type="number" :min="1" :max="100" placeholder="" />
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('itemCreateForm')"></el-button>
        <el-button type="info" @click="$router.push('/system/menu')"></el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
export default {
  name: 'menuItemCreate',
  data () {
    return {
      selectOptions: [],
      formData: {
        parentId: 0,
        name: '',
        url: '',
        css: '',
        sort: 1
      },
      rules: {
        parentId: [ { required: true, message: '' } ],
        name: [
          { required: true, message: '' },
          { max: 20, message: '20' }
        ],
        // url: { required: true, message: '' },
        css: { required: true, message: '' },
        sort: { required: true, message: '' }
      }
    }
  },
  created () {
    this.fetchMenuList()
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios.post('/api-b/menus', this.formData).then(() => {
            this.$router.push('/system/menu')
          }).catch(error => console.log(error))
        }
      })
    },
    // 
    fetchMenuList () {
      this.$axios.get('/api-b/menus/all').then(response => {
        this.selectOptions = response.data
      }).catch(error => console.log(error))
    }
  }
}
</script>

<style lang="scss" scoped>
.search-container {
  margin-bottom: 20px;
}
.search-left {
  float: left;
}
.search-right {
  float: right;
  text-align: right;
}
</style>
