<template lang="html">
  <section class="user-info">
    <el-form :model="user" :rules="rules" label-width="100px" ref="userInfoForm">
      <el-form-item label="">
        <el-input v-model="user.username" disabled />
      </el-form-item>

      <el-form-item label="" prop="nickname">
        <el-input v-model="user.nickname" placeholder="" />
      </el-form-item>

      <el-form-item label="" prop="sex">
        <el-select v-model="user.sex" placeholder="">
          <el-option label="" :value="1" />
          <el-option label="" :value="0" />
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('userInfoForm')"></el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
export default {
  name: 'UserActiveInfoUpdate',
  data () {
    return {
      user: {
        username: '',
        nickname: '',
        sex: ''
      },
      rules: {
        nickname: [
          { required: true, message: '' },
          { max: 20, message: '20' }
        ],
        sex: { required: true }
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      const { id, username, nickname, sex } = this.$store.state.app.user
      this.user = { id, username, nickname, sex }
    })
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$httpAPI.putUserInfo(this.user).then(response => {
            this.$message.success('')
            this.$router.push({ name: 'HomePage' })
          }).catch(error => {
            console.log(error)
            this.$message.error('')
          })
        } else {
          this.$message.warning('')
        }
      })
    }
  }
}
</script>
