<template lang="html">
  <section class="roles-list">
    <!--  -->
    <div class="search-container clearfix">
      <div class="search-left">
        <SearchPlain @on-input="handleSearchInfo" :placeholder="''" />
      </div>
      <div class="search-right">
        <router-link to="/system/role/create">
          <el-button icon="el-icon-circle-plus-outline" size="small" type="primary"></el-button>
        </router-link>
      </div>
    </div>
    <!--  -->
    <div class="table-roles">
      <el-table
        :data="tableActiveData"
        size="small"
        highlight-current-row
        border
      >
        <el-table-column prop="code" label="Code"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="createTime" label="" >
          <template slot-scope="scope">
            <span v-localtime="scope.row.createTime"></span>
          </template>
        </el-table-column>
        <el-table-column prop="operations" label="" min-width="300px">
          <template slot-scope="scope">
            <el-button
              @click="$router.push(`/system/role/${scope.row.id}/update`)"
              icon="el-icon-edit"
              size="mini"
              type="primary"
            />

            <el-button
              @click="showDialogDelete(scope.row)"
              icon="el-icon-delete"
              size="mini"
              type="primary"
            />

            <el-button
              @click="showDialogAuthorization(scope.row.id)"
              size="mini"
              type="primary"
            ></el-button>

            <el-button
              @click="showDialogMenu(scope.row.id)"
              size="mini"
              type="primary"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <DialogDeleteConfirm
      @on-confirm="handleDialogDeleteConfirm"
      :name="deletingRole.name"
      title=""
      ref="dialogDelete"
    />
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogAuthorizationVisible"
      width="560px"
      center
    >
      <el-transfer
        v-model="transferCurrentData"
        :data="transferData"
        :filter-method="transferFilterMethod"
        :titles="[ '', '' ]"
        filter-placeholder=""
        filterable
      />
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogTreeVisible"
      width="60%"
      center
    >
      <div class="tree-container">
        <el-tree
          :data="treeData"
          @check-change="handleCheckChange"
          empty-text=""
          show-checkbox
          highlight-current
          default-expand-all
        />
      </div>
    </el-dialog>
  </section>
</template>

<script>
import SearchPlain from '@/components/base/SearchPlain'
import DialogDeleteConfirm from '@/components/dialog/DialogDeleteConfirm'

export default {
  name: 'rolesList',
  components: {
    SearchPlain,
    DialogDeleteConfirm
  },
  data () {
    return {
      tableData: [], // 
      tableActiveData: [], // 
      deletingRole: { name: '' }, // 
      dialogAuthorizationVisible: false,
      authorizations: [],
      transferData: [],
      transferCurrentData: [],
      dialogTreeVisible: false,
      treeData: []
    }
  },
  created () {
    this.fetchUsersRoles()
  },
  methods: {
    // 
    handleSearchInfo (keyword) {
      this.tableActiveData = this.$_.filter(this.tableData, item => item.name.includes(keyword))
    },
    // 
    showDialogDelete (role) {
      this.deletingRole = role
      this.$refs.dialogDelete.toggleDialogStatus(true)
    },
    // 
    handleDialogDeleteConfirm () {
      this.$axios.delete('/api-u/roles/' + this.deletingRole.id).then(() => {
        this.$refs.dialogDelete.toggleDialogStatus(false) // 
        this.fetchUsersRoles() // 
        this.$message.success('')
      }).catch(() => this.$message.error(''))
    },
    // 
    showDialogAuthorization (id) {
      if (!this.transferData.length) {
        this.$axios.get(`/api-u/roles/${id}/permissions`).then(response => {
          response.data.forEach(item => {
            this.transferData.push({
              key: item.id,
              label: item.name,
              disabled: false
            })
          })
          this.dialogAuthorizationVisible = true
        })
      } else {
        this.dialogAuthorizationVisible = true
      }
    },
    transferFilterMethod (query, item) {
      return item.label.indexOf(query) > -1
    },
    // 
    showDialogMenu (id) {
      if (!this.treeData.length) {
        this.$axios.get('api-b/menus/tree').then(response => {
          response.data.forEach((group, i) => {
            this.treeData.push({
              id: group.id,
              label: group.name,
              disabled: false,
              children: []
            })
            group.child.forEach((item, j) => {
              this.treeData[i].children.push({
                id: item.id,
                label: item.name,
                disabled: false
                // children: []
              })
            })
          })
          this.dialogTreeVisible = true
        })
      } else {
        this.dialogTreeVisible = true
      }
    },
    handleCheckChange () {
      console.log('handleCheckChange')
    },
    // 
    fetchUsersRoles () {
      this.$axios.get('/api-u/roles').then(response => {
        this.tableData = this.tableActiveData = response.data.data
      }).catch(error => {
        console.log(error)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.search-container {
  margin-bottom: 20px;
}
.search-left {
  float: left;
}
.search-right {
  float: right;
  text-align: right;
}
.tree-container {
  padding-bottom: 20px;
}
</style>
