// 
router.beforeEach((to, from, next) => {
  // 
  document.title = to.meta.title

  const tokenExisted = !!localStorage.getItem('access_token')

  if (to.path === '/login/username' || to.path === '/login/phone') {
    // access_token
    // 
    if (tokenExisted) {
      next('/home')
    } else {
      next()
    }
  } else {
    // access_token
    // 
    if (tokenExisted) {
      next()
    } else {
      next('/login/username')
    }
  }
})

export default router
