<template lang="html">
  <section class="game-preview">
    <el-form
      :model="formData"
      :rules="rules"
      label-width="130px"
      ref="gameForm"
    >
      <el-form-item label="" prop="name">
        <el-input v-model="formData.name" placeholder="" />
      </el-form-item>

      <el-form-item label="" prop="startTime">
        <BaseTimePicker :second="formData.startTime" @on-change="handleTimePickerChange" />
      </el-form-item>

      <el-form-item label="" prop="endTime">
        <BaseTimePicker :second="formData.endTime" @on-change="handleTimePickerChange" />
      </el-form-item>

      <el-form-item label="" prop="intervalTime">
        <BaseTimePicker :second="formData.intervalTime" @on-change="handleTimePickerChange" />
      </el-form-item>

      <el-form-item label="" prop="url">
        <el-input v-model="formData.url" placeholder="" />
      </el-form-item>

      <el-form-item label="crontab" prop="crontab">
        <el-input v-model="formData.crontab" placeholder="crontab" />
      </el-form-item>

      <el-form-item label="crontab" prop="myCrontab">
        <el-input v-model="formData.myCrontab" placeholder="crontab" />
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="formData.remark" type="textarea" placeholder="" />
      </el-form-item>

      <el-form-item>
        <el-button @click="submitForm('gameForm')" type="primary"></el-button>
        <el-button @click="$router.push({ name: 'SystemLotteryList' })" type="info"></el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
import BaseTimePicker from '@/components/base/BaseTimePicker'

export default {
  components: {
    BaseTimePicker
  },
  data () {
    return {
      formData: {
        name: '',
        startTime: 0,
        endTime: 0,
        url: '',
        intervalTime: 0,
        crontab: '',
        myCrontab: '',
        remark: ''
      },
      rules: {
        name: [
          { required: true, message: '' },
          { max: 30, message: '30' }
        ],
        startTime: { required: true, message: '' },
        endTime: { required: true, message: '' },
        intervalTime: { required: true, message: '' }
        // url: [
        // { required: true, message: '' }
        // { pattern: /(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/, message: '' }
        // ],
        // crontab: { required: true, message: '' },
        // myCrontab: { required: true, message: '' }
      }
    }
  },
  created () {
    this.fetchGameInfo()
  },
  methods: {
    //  formData 
    handleTimePickerChange (value, propertyName) {
      console.log(this)
      console.log(value, propertyName)
      // this.formData.startTime = this._transferTimeToSeconds(value)
    },
    // 
    submitForm (fromName) {
      this.$refs[fromName].validate(valid => {
        if (valid) {
          this.$axios.post('/api-g/GameTypeConfig/save', {
            id: this.formData.id,
            startTime: this.formData.startTime
          }).then(() => {
            this.$message.warning('')
          }).catch(error => {
            console.log(error)
            this.$message.error('')
          })
        } else {
          this.$message.warning('')
        }
      })
    },
    // 
    fetchGameInfo () {
      this.$httpAPI.fetchLotterySettingInfo({ params: { id: this.$route.params.gameId } }).then(response => {
        this.formData = response.data.data
      }).catch(error => console.log(error))
    }
  }
}
</script>
