<template lang="html">
  <section class="vip-bets">
    <!--  -->
    <FilterArea />
    <!--  -->
    <div class="table-list">
      <!--  -->
      <ul v-if="tableDataReady" class="user-info-list clearfix">
        <li class="user-info-item">
          <span>ID</span>
          <span class="user-info-detail">{{tableData[0].id}}</span>
        </li>
        <li class="user-info-item">
          <span>ID</span>
          <span class="user-info-detail">{{tableData[0].userId}}</span>
        </li>
        <!-- <li class="user-info-item">
          <span></span>
          <span class="user-info-detail">{{tableData[0].username}}</span>
        </li> -->
        <li class="pull-right">
          <el-button @click="$router.push(`/users/${tableData[0].userId}/betsStatistics`)" type="primary" size="mini">15</el-button>
        </li>
      </ul>
      <!--  -->
      <el-table
        :data="tableData"
        size="small"
        highlight-current-row
        border
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="orderId" label="ID" :min-width="140"></el-table-column>

        <el-table-column prop="createDate" label="" :min-width="140">
          <template slot-scope="scope">
            <span>{{scope.row.createDate | time}}</span>
          </template>
        </el-table-column>

        <el-table-column prop="currentDrawno" label=""></el-table-column>

        <el-table-column prop="betsType" label="">
          <template slot-scope="scope">
            <span>{{scope.row.betsType | betType}}</span>
          </template>
        </el-table-column>

        <!-- <el-table-column prop="bets" label=""></el-table-column> -->
        <el-table-column prop="totalBets" label="" :width="70">
          <template slot-scope="scope">
            <span>{{scope.row.totalBets}}</span>
            <BaseMore @on-click="showDialogBetsDetails(scope.row.bet.bettings)" class="pull-right" />
          </template>
        </el-table-column>

        <el-table-column prop="totalAmount" label="" :min-width="60">
          <template slot-scope="scope">
            <span>{{scope.row.totalAmount | RMB}}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="orderType" label=""></el-table-column> -->

        <el-table-column prop="status" label="">
          <template slot-scope="scope">
            <span>{{scope.row.status | betStatus}}</span>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @current-change="handleCurrentChange"
        @size-change="handleSizeChange"
        :current-page="page.current"
        :total="page.total"
        :page-sizes="[10, 20, 40, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
      />
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      width="80%"
      center
    >
      <div>
        <el-row :gutter="20">
          <el-col :sm="12" :md="8" :lg="6" :xl="4" v-for="(item, i) in currentBets" :key="i">
            <el-card>
              <ul>
                <li class="balls-row clearfix">
                  <span></span>
                  <div class="balls-container">
                    <template v-for="(ball, j) in item.ballNums">
                      <BaseBall :number="ball" :key="j" />
                    </template>
                  </div>
                </li>
                <li class="balls-row clearfix">
                  <span></span>
                  <span class="balls-container">
                    <BaseBall />
                  </span>
                </li>
                <li>
                  <span></span>
                  <span class="pull-right">{{item.childType | betSubtype }}</span>
                </li>
                <li>
                  <span></span>
                  <span class="pull-right">{{item.betAmount | RMB}}</span>
                </li>
                <li>
                  <span></span>
                  <span class="pull-right">{{item.rate}}</span>
                </li>
                <li>
                  <span></span>
                  <span class="pull-right">{{item.betResulte | betResult}}</span>
                </li>
              </ul>
            </el-card>
          </el-col>
        </el-row>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import FilterArea from '@/components/others/FilterArea'
import BaseBall from '@/components/base/BaseBall'
import BaseMore from '@/components/base/BaseMore'

export default {
  components: {
    FilterArea,
    BaseBall,
    BaseMore
  },
  data () {
    return {
      dialogVisible: false,
      tableDataReady: false,
      tableData: [],
      page: {
        total: 10,
        current: 1,
        size: 10
      },
      currentBets: []
    }
  },
  created () {
    this.fetchUserList({ current: this.page.current = 1, size: this.page.size = 10 })
  },
  methods: {
    // 
    handleCurrentChange (currentPage) {
      this.fetchUserList({ current: this.page.current = currentPage, size: this.page.size })
    },
    // 
    handleSizeChange (pageSize) {
      this.fetchUserList({ current: this.page.current = 1, size: this.page.size = pageSize })
    },
    // 
    showDialogBetsDetails (bets) {
      this.dialogVisible = true
      this.currentBets = bets
    },
    // 
    handleClose () {
      this.dialogVisible = false
    },
    fetchUserList (page) {
      this.$axios.post('/api-g/getUserBetsInfo', {}, {
        params: { id: this.$route.params.id, pageNo: this.page.current, pageSize: this.page.size }
      }).then(response => {
        this.tableData = response.data.results// 
        this.page.total = response.data.amount // 
        this.tableDataReady = true
      }).catch(error => console.log(error))
    }
  }
}
</script>

