<template lang="html">
  <section class="users-list">
    <!--  -->
    <div class="search-container clearfix">
      <!--   -->
      <div class="search-left">
        <div class="input-group">
          <span></span>
          <el-input
            size="small"
            placeholder=""
            clearable
          />
        </div>

        <div class="input-group">
          <span></span>
          <el-input
            size="small"
            placeholder=""
            clearable
          />
        </div>

        <div class="input-group">
          <el-button
            type="primary"
            size="small"
          >
          </el-button>
        </div>
      </div>
      <!--   -->
      <div class="search-right">
        <router-link to="/admin/user/create">
          <el-button icon="el-icon-circle-plus-outline" size="small" type="primary"></el-button>
        </router-link>
      </div>
    </div>
    <!--  -->
    <div class="table-container">
      <!--  -->
      <el-table
        :data="tableData"
        size="small"
        highlight-current-row
        border
      >
        <el-table-column type="index" :width="40"></el-table-column>

        <el-table-column prop="id" label="ID" :min-width="90"></el-table-column>

        <el-table-column prop="createTime" label="" :min-width="140">
          <template slot-scope="scope">
            <span>{{scope.row.createTime | time}}</span>
          </template>
        </el-table-column>

        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="nickname" label=""></el-table-column>
        <el-table-column prop="type" label=""></el-table-column>

        <el-table-column prop="banlance" label="" :min-width="120">
          <template slot-scope="scope">
            <span>{{scope.row.banlance | RMB}}</span>
          </template>
        </el-table-column>

        <el-table-column prop="enabled" label="" :width="120">
          <template slot-scope="scope">
            <span>{{scope.row.enabled ? '' : ''}}</span>
          </template>
        </el-table-column>

        <el-table-column prop="operations" label="" :min-width="380">
          <template slot-scope="scope">
            <el-button
              @click="$router.push(`/admin/user/${scope.row.id}/update`)"
              icon="el-icon-edit"
              size="mini"
              type="primary"
            />

            <el-button
              @click="$refs.dialog.toggleDialogStatus(true)"
              type="primary"
              size="mini"
            ></el-button>

            <el-button
              @click="$message.warning('...')"
              type="primary"
              size="mini"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @current-change="handleCurrentChange"
        @size-change="handleSizeChange"
        :current-page="page.current"
        :total="page.total"
        :page-sizes="[10, 20, 40, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
      />
    </div>
    <!--  -->
    <DialogInput
      @on-confirm="handleDialogConfirm"
      value="123456"
      title=""
      ref="dialog"
    />
  </section>
</template>

<script>
import DialogInput from '@/components/dialog/DialogInput'

export default {
  components: {
    DialogInput
  },
  data () {
    return {
      tableData: [],
      page: {
        total: 10,
        current: 1,
        size: 10
      },
      dialogVisible: false,
      currentUser: {}
    }
  },
  created () {
    this.fetchUserList({ start: 0, length: 10 })
  },
  methods: {
    // 
    handleCurrentChange (currentPage) {
      this.fetchUserList({ start: (this.page.current = currentPage) - 1, length: this.page.size })
    },
    // 
    handleSizeChange (pageSize) {
      this.fetchUserList({ start: (this.page.current = 1) - 1, length: this.page.size = pageSize })
    },
    //  $emit 
    handleDialogConfirm () {
      this.$message.warning('...')
    },
    fetchUserList (page) {
      this.$axios.get('/api-u/users', {
        params: { start: page.start * 10, length: page.length }
      }).then(response => {
        // 
        this.tableData = response.data.data
        // 
        this.page.total = response.data.total
      }).catch(error => console.log(error))
    }
  }
}
</script>

