import axios from 'axios'
import router from '@/router'
import { Loading } from 'element-ui'

let loading
function startLoading () {
  loading = Loading.service({
    lock: true,
    text: '...',
    background: 'rgba(0, 0, 0, 0.7)'
  })
}
function endLoading () {
  loading.close()
}

/* axios */
axios.defaults.baseURL = 'http://192.168.5.182:8080'
// Token; Token;
if (localStorage.getItem('access_token')) {
  const AUTO_TOKEN = `${localStorage.getItem('token_type')} ${localStorage.getItem('access_token')}`
  axios.defaults.headers.common['Authorization'] = AUTO_TOKEN
}

// 
axios.interceptors.request.use(config => {
  // httploading
  startLoading()
  return config
}, error => {
  return Promise.reject(error)
})

// 
axios.interceptors.response.use(response => {
  // http 
  endLoading()
  return response
}, error => {
  endLoading() // 

  if (error.error === 'invalid_token') {
    localStorage.removeItem('access_token')
    localStorage.removeItem('refresh_token')
    localStorage.removeItem('token_type')

    axios.defaults.headers.common['Authorization'] = ''
    console.log('Token')
    return
  }

  switch (error.toString().substr(-3)) {
    case '401':
      localStorage.removeItem('access_token')
      localStorage.removeItem('refresh_token')
      localStorage.removeItem('token_type')

      router.push('/login/username')
      axios.defaults.headers.common['Authorization'] = ''
      console.log('Token')
      break
    case '403':
      console.log('HTTPToken')
      break
    case '404':
      console.log('HTTP')
      break
    case '500':
      console.log(`${error.data.messge}`)
      break
    default:
      console.log(`${error.data.messge}`)
  }
  return Promise.reject(error)
})

export default axios
